import styles from "./Demos.module.scss";
//import { v4 as uuid } from "uuid";
import { localeContext } from "../../context/localeContext.js";
import React, {  useState,useContext, memo } from "react";

type Demo = {
    name: string;
    figures: {
        path: string;
    }[];
}

const Experiment: React.FC<{ data: Demo }> = ({ data }) => {
  const { name, figures } = data;
  const [showPopup, setShowPopup] = useState(false);
  const [currentImagePath, setCurrentImagePath] = useState("");

  const openPopup = (path: string) => {
    setCurrentImagePath(path);
    setShowPopup(true);
  };

  const closePopup = () => {
    setShowPopup(false);
  };

  return (
    <section>
      <h3>{name}</h3>
      <hr />
      <div className={styles["figures-wrapper"]}>
        {figures.map((imgData, i) => (
          <button key={i} onClick={() => openPopup(imgData.path)}>
            <img src={imgData.path} className={styles.figure} alt="" />
          </button>
        ))}
      </div>

      {showPopup && (
        <div
          id="outerdiv"
          style={{
            position: "fixed",
            top: 0,
            left: 0,
            background: "rgba(0,0,0,0.7)",
            zIndex: 2,
            width: "100%",
            height: "100%",
            display: "flex",
            justifyContent: "center",
            alignItems: "center",
          }}
          onClick={closePopup}
        >
          <div id="innerdiv" style={{ position: "absolute" }}>
            <img
              id="bigimg"
              style={{ border: "5px solid #fff", maxWidth: "100%", maxHeight: "100%" }}
              src={currentImagePath}
              alt=""
            />
          </div>
        </div>
      )}
    </section>
  );
};


function Demos(){
  const { demos } = useContext(localeContext);
  return <div className={styles.demos}>
    {demos.map((demo: Demo, i: number) => <Experiment key={i} data={demo}/>)}
    <br/>
  </div>;
}

export default memo(Demos);
